import { api } from '@shared/api';
import { useQuery } from '@tanstack/react-query';
import { useCallback, useState } from 'react';
export const DefaultTransferOption = 'all-transfers';
export const transferOptions = [
    {
        name: 'All',
        key: 'all-transfers',
    },
    {
        name: 'No transfers',
        key: '0-transfers',
    },
    {
        name: '1 transfer',
        key: '1-transfer',
    },
    {
        name: '2 transfers',
        key: '2-transfers',
    },
    {
        name: '3 transfers',
        key: '3-transfers',
    },
];
export const currencies = ['USD', 'RUB', 'EUR'];
export const useAirplaneTickets = () => {
    const [selectedTransferFilters, setSelectedTransferFilters] = useState([DefaultTransferOption]);
    const [selectedCurrency, setSelectedCurrency] = useState('RUB');
    const onTransferFiltersChange = useCallback((key) => {
        setSelectedTransferFilters((prevState) => {
            let newState;
            if (key === DefaultTransferOption) {
                return [DefaultTransferOption];
            }
            if (prevState.includes(key)) {
                newState = prevState.filter((k) => k !== key);
            }
            else {
                newState = [...prevState, key];
            }
            newState = newState.filter((opt) => opt !== DefaultTransferOption);
            if (newState.length === 0)
                return [DefaultTransferOption];
            return newState;
        });
    }, []);
    const { data, isLoading, isError } = useQuery(['airplaneTickets', selectedTransferFilters, selectedCurrency], () => api.getAirplaneTickets({
        transferFilters: selectedTransferFilters,
        currency: selectedCurrency,
    }));
    return {
        selectedTransferFilters,
        onTransferFiltersChange,
        selectedCurrency,
        onCurrencyChange: setSelectedCurrency,
        isLoading,
        isError,
        airplaneTickets: data,
    };
};
